"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { CheckCircle2, FileText } from "lucide-react";

export default function Sidebar() {
  const pathname = usePathname();

  const menuItems = [
    { label: "Dashboard", href: "/staff/dashboard", icon: <CheckCircle2 className="w-5 h-5" /> },
    { label: "View All Cases", href: "/staff/cases", icon: <FileText className="w-5 h-5" /> },
  ];

  return (
    <aside className="fixed top-0 left-0 h-screen w-64 bg-white border-r border-gray-200 pt-28 px-4">
      <nav className="flex flex-col space-y-4">
        {menuItems.map((item) => {
          const isActive = pathname === item.href;
          return (
            <Link
              key={item.href}
              href={item.href}
              className={`flex items-center gap-3 px-4 py-2 rounded-lg font-medium ${
                isActive
                  ? "bg-teal-600 text-white"
                  : "text-gray-700 hover:bg-gray-100 transition"
              }`}
            >
              {item.icon}
              {item.label}
            </Link>
          );
        })}
      </nav>
    </aside>
  );
}
